import uniBadge from '@/components/uni-badge/uni-badge.vue'
export default {
	components: {
		uniBadge
	},
	data() {
		return {
			forum: null, // 课程信息
			bundleId: 3, // 系列标识
		}
	},
	onLoad() {
		this.$http.request({
			url: 'forum?ForumSearch[bundle_id]=' + this.bundleId,
			method: 'get',
		}).then(res => {
			this.forum = res.data.items[0]
		}).catch(console.log)
	}
}
